import React, { Dispatch, ReactNode, SetStateAction, useMemo } from "react"
import { Box, Typography } from "@rp/react/web/mui/core"
import { MultiCheckboxSelect } from ".."

type SelectItem = {
  value: string
  label: string
}

type OptionsSelectProps = {
  isLoading?: boolean
  items: Array<SelectItem>
  label: string
  placeholder: string
  selected: Array<SelectItem>
  setSelected: (selected: Array<SelectItem>) => void | Dispatch<SetStateAction<Array<SelectItem>>>
  CustomSelectionLine: (props: SelectItem & { toggle: (item: SelectItem) => void }) => ReactNode
  search: string
  setSearch: (s: string) => void
}

function DefaultSelectionLine({ label, value }: { label: string; value: string }) {
  return <Typography key={value}>{label}</Typography>
}

function OptionsSelect({
  isLoading,
  items,
  label,
  placeholder,
  selected,
  setSelected,
  CustomSelectionLine,
  search,
  setSearch,
}: OptionsSelectProps) {
  const renderItem = useMemo(
    () =>
      CustomSelectionLine
        ? (
            props: SelectItem,
            _index: number,
            _search: string,
            toggle: (item: SelectItem) => void,
          ) => <CustomSelectionLine {...props} toggle={toggle} />
        : DefaultSelectionLine,
    [CustomSelectionLine],
  )

  return (
    <Box>
      <MultiCheckboxSelect
        onChange={setSelected}
        label={label}
        placeholder={placeholder}
        items={items}
        value={selected}
        isLoading={isLoading}
        renderItem={renderItem}
        search={search}
        onSearch={setSearch}
      />
    </Box>
  )
}

export { OptionsSelect }
